import Vue from 'vue'
import VueRouter from 'vue-router'

// 使用路由插件
Vue.use(VueRouter)

// 路由页面组件
const Home = resolve => require(['../views/index/index.vue'], resolve)

// 路由配置
const routes = [
  {
    path: '/',
    name: 'index',
    component: Home
  },
  {
    path: '/Install',
    name: 'install',
    component: resolve => require(['../views/index/install.vue'], resolve)
  },
  {
    path: '/Components/alert',
    name: 'alert',
    component: resolve => require(['../views/components/alert.vue'], resolve)
  }
]

// 实例路由
const router = new VueRouter({ routes })



// 进入新页面显示 Loading
// router.beforeEach((to, from, next) => {
//   // console.log(router.app)
//   // to.app.$Loading.start()
//   next()
// })

// 页面加载完成后隐藏 Loading
// router.afterEach(route => {
//   // to.app.$Loading.finish()
//   // next()
//   // console.log(route)
// })

export default router



