<template>
  <ul class="ex-menu">
    <ex-menu-item
      class="ex-menu-item"
      v-for="menuItem in menu"
      v-bind:menuItem="menuItem"
    >
    </ex-menu-item>
  </ul>
</template>

<script>
  import exMenuItem from './menuItem.vue'

  export default {
    name: 'ex-menu',
    props: {
      menu: {
        type: Array,
        required: true
      }
    },
    components: {
      exMenuItem
    }
  }
</script>



