import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'

import App from './App'
import router from './router/index'
import highlight from 'highlight.js'

Vue.use(VueResource)
Vue.use(VueRouter)

// vue 2.0 vue-router2
const app = new Vue({
  // el: '#app',
  render: h => h(App),
  router
}).$mount('#app')



