<style>
html,body{
    height: 100%;
    margin: 0;
}
.page-wrapper {
    background: #f1f4f9;
    padding: 1rem 4rem 4rem 4rem;
}
.ex-header {
    display: flex;
    height: 4rem;
    width: 100%;
    padding: 0 3rem;
    z-index: 999;
    background: #2db7f5;
    box-sizing: border-box;
}
.ex-header-logo {
    padding-top: .5rem;
    width: 12rem;
}
.ex-header-logo-img {
    display: none;
    height: 2rem;
    margin: 1rem 1rem 0 0;
}
.ex-header-logo-text {
    color: #fff;
    font-size: 1.5rem;
    font-weight: 100;
    line-height: 3rem;
}
.ex-header-nav {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}
.ex-header-nav-item {
    color: #fff;
    text-align: center;
    font-size: .9rem;
    line-height: 3.9rem;
    min-width: 4rem;
    cursor: pointer;
}
.ex-header-nav-item.active,
.ex-header-nav-item:hover {
    color: #fff;
    border-bottom: .2rem solid #fff;
}
.container {
    position: relative;
    display: flex;
    height: 100%;
    background: #fff;
    box-sizing: border-box;
    overflow-x: hidden;
}
.sidebar {
    width: 15rem;
    height: 100%;
    margin-bottom: 5rem;
    background-color: #fff;
    flex-shrink: 0;
}
.content{
    min-width: 40rem;
    height: 100%;
    padding: 1rem;
    background: #fff;
    border-left: 1px solid #f7f7f7;
}
.ex-container {
    display: flex;
}
.ex-menu-container {
    min-height: 40rem;
}
.ex-menu-item {
    position: relative;
    cursor: pointer;
}
.ex-menu-title-container {
    font-size: 1rem;
    padding: 1rem 1.5rem;
}
.ex-menu-sub-title,
.ex-sub-menu-sub-title{
    color: #666;
    opacity: .67;
}
.ex-menu-title:hover,
.ex-sub-menu-item:hover {
    background: #eaf8fe;
}
.ex-sub-menu-item {
    font-size: .8rem;
    cursor: pointer;
}
.ex-sub-menu-item > a {
    display: block;
    padding: 1rem 2rem;
}
.ex-sub-menu-item > a.router-link-active,
.ex-sub-menu-item > a.router-link-active .ex-sub-menu-sub-title {
    font-weight: 700;
    color: #2db7f5;
}
.ex-menu-title-arrow {
    position: absolute;
    font-size: .5rem;
    right: 1rem;
    top: 1.25rem;
    transform: rotate(0);
    transition: transform 0.2s ease;
}
.ex-menu-item.open .ex-menu-title-arrow {
    transform: rotate(180deg);
}
.ex-sub-menu-item.active {
    color: #2db7f5;
}
.ex-card {
    margin: 2rem 1rem;
}
.ex-menu-group-title {
    color: #b9b9b9;
    font-size: .8rem;
    padding-left: 1.5rem;
}
.show-slider {
    display: none;
    position: fixed;
    font-size: 2rem;
    z-index: 3;
    color: #fff;
    width: 4rem;
    text-align: center;
    height: 4rem;
    line-height: 4rem;
    top: 0;
    right: 0;
}
.ex-footer {
    display: flex;
    padding: 2rem 4rem 5rem 4rem;
    background-color: #67cdfb;
}
.ex-footer-nav {
    display: flex;
    width: 50%;
}
.ex-footer-info {
    color: #fff;
    font-size: .8rem;
    width: 50%;
    text-align: right;
}
.ex-footer-info a {
  color: #fff;
}
.ex-footer-nav-item {
    flex: 1;
}
.ex-footer-nav-title {
    color: #fff;
}
.ex-footer-nav-link {
    font-size: .8rem;
    margin: .25rem 0;
}
.ex-footer-nav-link-a {
    color: #fff;
}
.ex-footer-nav-link-description {
    color: #fff;
}
.demo-content {
    width: 20rem;
}
.slide-left-transition {
  transition: transform .3s ease;
  transform: translateX(0)
}
.slide-left-enter,
.slide-left-leave {
    transform: translateX(-100%)
}
@media screen and (max-width: 1000px) {
    .ex-header {
        padding: 0 1rem;
        position: fixed;
    }
    .page-wrapper {
        padding: 0;
    }
    .container {
        padding-left: 0;
    }
    .content {
        min-width: initial;
        padding: 4rem 1rem;
        box-sizing: border-box;
        overflow: hidden;
    }
    .show-slider {
        display: inline-block;
        width: 4rem;
        text-align: center;
        height: 4rem;
        line-height: 4rem;
        top: 0;
        right: 0;
    }
    .sidebar {
        position: fixed;
        z-index: 1000;
        top: 0;
        overflow-y: auto;
    }
    .ex-header-nav {
        display: none;
    }
    .ex-footer {
        padding: .5rem 1rem;
        font-size: .8rem;
        flex-wrap: wrap;
    }
    .ex-footer-nav {
        width: 100%;
    }
    .ex-footer-nav-item {
        text-align: center;
    }
    .ex-footer-info {
        width: 100%;
        text-align: center;
    }
    .ex-footer-nav-link {
        font-size: .6rem;
    }
}
</style>

<template>
    <div>
        <div class="loadingbar-wrapper" v-show="state.loading.show">
            <div id="loadingbar" class="waiting" :style="{ width: state.loading.progress+'%'}">
                <dt></dt><dd></dd>
            </div>
        </div>
        <header class="ex-header">
            <router-link to="/">
                <div class="ex-header-logo">
                    <span class="ex-header-logo-text">{{ appName }}</span>
                </div>
            </router-link>
            <div class="show-slider" @click="sliderShow = !sliderShow">
                <span class="ion-navicon"></span>
            </div>
            <nav class="ex-header-nav">
                <router-link v-for="item in header.nav" :to="item.route">
                    <div class="ex-header-nav-item" :class="{ 'active': item.active }">
                        <span class="ex-header-nav-text">{{item.title}}</span>
                    </div>
                </router-link>
            </nav>
        </header>
        <div class="page-wrapper">
            <div class="container">
                <div class="sidebar" v-show="sliderShow" transition="slide-left">
                    <div class="ex-menu-container">
                        <ex-menu :menu="menu" @click="mobileClick"></ex-menu>
                    </div>
                </div>
                <div class="content">
                    <router-view></router-view>
                </div>
            </div>
        </div>
        <footer class="ex-footer">
            <nav class="ex-footer-nav">
                <div class="ex-footer-nav-item" v-for="item in footer.nav">
                    <span class="ex-footer-nav-title">{{item.title}}</span>
                    <div class="ex-footer-nav-link" v-for="link in item.links">
                        <a  class="ex-footer-nav-link-a" :href="link.link">{{link.title}}</a>
                        <span class="ex-footer-nav-link-description">{{link.sub}}</span>
                    </div>
                </div>
            </nav>
            <div class="ex-footer-info">
                <p>©2016 筷子科技前端</p>
                <p>Theme by <a href="https://luojilab.github.io/radon-ui" target="_blank">罗辑思维前端团队</a></p>
            </div>
        </footer>
    </div>

</template>

<script>
  // radon-ui 样式
  import './assets/css/dist.css'
  import './assets/css/loadingbar.css'
  // 代码高亮样式
  import 'highlight.js/styles/github.css'
  import 'github-markdown-css'

  //- 配置
  import { header, menu, footer } from './config'

  //- 组件
  import exMenu from './components/menu.vue'

  // 组件入口
  export default {
    // 页面数据配置
    data() {
      return {
        appName: 'gallery',
        // 左侧导航展开标记
        sliderShow: document.body.offsetWidth > 1000,
        menu: menu,
        // 头部配置
        header: header,
        // 页脚配置
        footer: footer,
        state: {
          timer: null,
          loading: {
            show: false,
            progress: 0
          }
        }
      }
    },
    // 页面就绪
    ready() {
      window.addEventLinstener('resize', this.resizeCheck)
    },
    // 使用组件
    components: {
      exMenu
    },
    methods: {
      errorDone () {
          this.state.loading.error = false
      },
      progressDone () {
          this.state.loading.progress = 0
      },
      resizeCheck() {
        // debounce
        if(this.state.timer) {
          clearTimeout(this.state.timer)
          this.state.timer = null
        }

        this.state.timer = setTimeout(() => {
          // 页面宽度大于768时 显示菜单栏
          if(document.body.offsetWidth > 768) {
            this.sliderShow = true
          }
        }, 200)
      },
      mobileClick() {
        if (document.body.offsetWidth < 768) {
          // 移动端隐藏菜单栏
          this.sliderShow = false
        }
      }
    },
    created () {
        this.$router.beforeEach((to, from, next) => {
            this.state.loading = {
                show: true,
                progress: 50
            }
            next()
        })

        this.$router.afterEach(route => {
            this.state.loading.progress = 100
            setTimeout(() => {
                this.state.loading = {
                    progress: 0,
                    show: false
                }
            }, 300)
        })
    }
  }
</script>



